* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.main {
  position: relative;
  width: 1440px;
  height: 960px;
  background: #ffffff;
}
.main-img {
  position: absolute;
  width: 505px;
  height: 330px;
  left: 230px;
  top: 96px;
}
.main-title {
  position: absolute;
  width: 380px;
  height: 50px;
  left: calc(50% - 190px + 300px);
  top: calc(50% - 25px - 319px);
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 42px;
  line-height: 120%;
  color: #121212;
}
.main-copy {
  position: absolute;
  width: 380px;
  height: 96px;
  left: calc(50% - 190px + 300px);
  top: calc(50% - 48px - 222px);
  font-family: "Roboto";
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
  color: #121212;
}
.main-btn {
  position: absolute;
  width: 128px;
  height: 48px;
  left: calc(50% - 190px + 300px);
  top: 338px;
  background: #e4c1ae;
  border-radius: 32px;
  border: none;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;
  color: #ffffff;
  transition: all 0.2s ease-in-out;
}
.main-btn:hover {
  box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, 0.75);
  text-shadow: 0px 1px 1px rgba(0, 0, 0, 0.75);
}

.popup {
  transition: all 0.2s ease-in-out;
  visibility: hidden;
  position: absolute;
  margin-top: -23px;
  width: 100vw;
  height: 130vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.popup-bg {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(18, 18, 18, 0.3137254902);
}
.popup-window {
  z-index: 2;
  position: relative;
  width: 920px;
  height: 210px;
  background-color: #ffffff;
}
.popup-window-btn {
  position: absolute;
  top: 46px;
  right: 46px;
  width: 25px;
  height: 25px;
  font-size: 15px;
  background: none;
  border: none;
  transition: all 0.2s ease-in-out;
}
.popup-window-btn:hover {
  background: rgba(60, 60, 60, 0.062745098);
  border-radius: 5px;
}
.popup-window-title {
  position: absolute;
  width: 380px;
  height: 50px;
  left: calc(50% - 190px - 175px);
  top: calc(50% - 25px - 24px);
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 42px;
  line-height: 120%;
  color: #121212;
}
.popup-window-content {
  position: absolute;
  width: 380px;
  height: 24px;
  left: calc(50% - 190px - 175px);
  top: calc(50% - 12px + 37px);
  font-family: "Roboto";
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
}
.popup-window-content-bold {
  font-weight: 900;
}
.popup-window-resetbtn {
  position: absolute;
  top: calc(40% - 12px + 37px);
  left: calc(120% - 190px - 175px);
  background: #e4c1ae;
  width: 80px;
  height: 45px;
  border-radius: 32px;
  border: none;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;
  color: #ffffff;
  transition: all 0.2s ease-in-out;
}
.popup-window-resetbtn:hover {
  box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, 0.75);
  text-shadow: 0px 1px 1px rgba(0, 0, 0, 0.75);
}

@media screen and (max-width: 770px) {
  .main-img {
    left: 0;
    right: 0;
    margin: 0 auto;
    top: 96px;
    width: 390px;
    height: 200px;
  }
  .main-title {
    left: 0;
    right: 0;
    margin: 0 auto;
    top: 350px;
  }
  .main-copy {
    left: 0;
    right: 0;
    margin: 0 auto;
    top: 400px;
  }
  .main-btn {
    left: 90px;
    right: 0;
    top: 530px;
  }
  .popup-window {
    position: absolute;
    width: 420px;
    height: 250px;
  }
  .popup-window-title {
    left: 130px;
    top: 50px;
  }
  .popup-window-content {
    left: 60px;
    top: 120px;
  }
  .popup-window-resetbtn {
    top: 170px;
    left: 155px;
  }
}
@media screen and (min-width: 771px) and (max-width: 1400px) {
  .main-img {
    left: 0;
    right: 0;
    margin: 0 auto;
    top: 96px;
    width: 505px;
    height: 330px;
  }
  .main-title {
    left: 0;
    right: 0;
    margin: 0 auto;
    top: 480px;
  }
  .main-copy {
    left: 0;
    right: 0;
    margin: 0 auto;
    top: 550px;
  }
  .main-btn {
    left: 0;
    right: 0;
    margin: 0 auto;
    top: 680px;
  }
  .popup-window {
    position: absolute;
    width: 580px;
    height: 250px;
  }
  .popup-window-title {
    left: 100px;
    top: 60px;
  }
  .popup-window-content {
    left: 60px;
    top: 140px;
  }
  .popup-window-resetbtn {
    top: 180px;
    left: 480px;
  }
}
::-webkit-scrollbar {
  width: 0px;
}

body {
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

.visible {
  visibility: visible;
  opacity: 1;
}

.invisible {
  visibility: hidden;
  opacity: 0;
}